import * as React from 'react';
import { Plugin } from '@ali/4ever-cangjie';
import Subject from '../components/subject';
import { isSubject } from '../utils';
import { SubjectConfig } from '../types';

function createRenderNode(config: SubjectConfig): Plugin['renderNode'] {
  return function renderNode(props, controller, next) {
    const {
      node,
      attributes,
      children,
    } = props;

    if (isSubject(node)) {
      return (
        <Subject
          controller={controller}
          node={node}
          attributes={attributes}
          config={config}
        >
          {children()}
        </Subject>
      );
    }

    return next();
  };
}

export default createRenderNode;
